<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process183"></a>

<p><br></p>
<p><span class="rvts17">对她和对其他人都是危险的（</span><span class="rvts50">A Danger to Her and Others</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p class="rvps2"><span class="rvts41">任务坐标：</span>
<br><span class="rvts20">X:666,Y:223</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：格里戈里厄斯</span><span class="rvts20"></span><span class="rvts21">斯万（</span><span class="rvts20">Gregorius Swann</span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">拯救娜塔莉</span><span class="rvts20"></span><span class="rvts21">布鲁姆赫德（</span><span class="rvts20">Natalie Bromhead</span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1202.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts19">可以在石墓花园不远处找到一处小农舍，门外有一男</span><span class="rvts20">NPC</span><span class="rvts21">格里戈里厄斯请求帮助治疗以为被恶魔侵害的女子。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1203.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts19">在进入地下室之后，如果想要拯救该女子，可以使队伍中具有</span><span class="rvts20"></span><span class="rvts21">学者（</span><span class="rvts20">Scholar</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">标签的角色与男主角进行对话，中间会出现</span><span class="rvts20"></span><span class="rvts21">请你治愈他</span><span class="rvts20"></span><span class="rvts21">之类的选项。</span>
<br><span class="rvts21">一旦出现该选项，那就意味着必须治好女子，如果该女子不幸身亡，那么将得到该支线的坏结局。</span>
<br><span class="rvts20"></span>
<br><span class="rvts21">在对战过程中，会遭遇该女子心中的四个恶魔（不同的颜色，分别代表着女子身上的愤怒，恐惧，绝望和苦恼），试图首先击败这四个具有不同颜色的恶魔。</span>
<br><span class="rvts21">然后不要杀掉女子，试图削弱她（降低她的血量，低于</span><span class="rvts20">40%</span><span class="rvts21">左右），变会出现治疗过程对话。</span>
<br><span class="rvts21">治疗过程相当简单，需要通过智力（</span><span class="rvts20">Intelligence</span><span class="rvts21">）或灵巧（</span><span class="rvts20">Finess</span><span class="rvts21">）两个测试（要求非常低），之后按照以下步骤进行选择：</span>
<br><span class="rvts20">1. </span><span class="rvts21">剥开皮肤，拿着一支骨锤开始轻轻的在她头骨上敲打。（</span><span class="rvts20">"Peel back the skin, take up a bone hammer and chisel, and start to gently tap away at her skull"</span><span class="rvts21">）</span>
<br><span class="rvts20">2. </span><span class="rvts21">拿出一对小镊子，轻轻的取走里面的蛆虫。（</span><span class="rvts20">"Select a pair of delicate tweezers and gently try to remove the maggot"</span><span class="rvts21">）</span>
<br><span class="rvts20">3. </span><span class="rvts21">最后慢慢的缝合伤口。（</span><span class="rvts20"> "Slowly close up the wound"</span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><span class="rvts21">如果成功治愈了女子，那么将得到男子的</span><span class="rvts20">50</span><span class="rvts21">态度值（降低商品价格），获得一把额外的史诗装备和额外的经验，并且之后，可以在这里寻求男子对自己的治疗。</span>
<br><span class="rvts21">如果失手害死了女子，那么男子将会降低</span><span class="rvts20">50</span><span class="rvts21">态度值，并且不能获得任何经验。但是如果在开始对女子进行治疗之前，如果对话的角色没有学者标签，并选择了</span><span class="rvts20"></span><span class="rvts21">杀掉她</span><span class="rvts20"></span><span class="rvts21">，并不会降低男子的态度值，而且之后男子也会治疗。但不会获得额外的史诗装备。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



